      municipioDestino: [''],
      direccionDestino: ['', Validators.required],
      finExportacion: ['', Validators.required],
      entidadDestino: [''],
      telefonoDestino: [''],
      tiempoPermanencia: [''],
      tipoPermanencia: [''],
