import { Listareporte, Reporte, ReturnModelObtenerReporte } from './../../models/returnmodelobtenerreporte';
import { RequestModelObtenerReporte } from './../../models/requestmodelobtenerreporte';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { AutorizarExportacionUtilService } from '../../services/autorizar-exportacion-util.service';
import { ReturnModelObtenerSolicitudes } from '../../models/returnmodelobtenersolicitudes';
import { Solicitudsalidaobra } from '../../models/solicitudsalidaobra';
import { ConsultarSolicitudForm } from './consultar-solicitud.form';
import { SolicitudProvider } from './../../../providers/solicitud';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-consultar-solicitud',
  templateUrl: './consultar-solicitud.component.html',
  styleUrls: ['./consultar-solicitud.component.scss']
})
export class ConsultarSolicitudComponent implements OnInit {
  src: string;
  seleccionForm: FormGroup;
  seleccionSolucionForm: ConsultarSolicitudForm;
  invalidForm: boolean;
  items: Array<any> = [];
  consultarVerConcepto: boolean;
  verInformacionDescargar: boolean;
  mensajeConsultaSinResultados: boolean;
  currentMessage: string;
  titulo: string;
  reporteRespuestaSolicitud: Reporte;
  reportesObras: Reporte[];
  archivoVisualizado: Reporte;
  visualizacion: boolean;

  constructor(
    private router: Router,
    public formBuilder: FormBuilder,
    public modalService: NgbModal,
    private solicitudProvider: SolicitudProvider,
    public service: AutorizarExportacionUtilService) {
    this.invalidForm = false;
    this.consultarVerConcepto = false;
    this.verInformacionDescargar = false;
    this.mensajeConsultaSinResultados = false;


  }

  establecerAreaNotificaciones(value: string) {
    const s = document.getElementsByTagName('govco-area-servicios');
    s[0].setAttribute('step', value);
  }

  consultar() {
    if (this.seleccionSolucionForm.isValid()) {
      this.service.ConsultarSolicitudesXRango(this.seleccionForm.value.numero_documento, this.seleccionForm.value.numero_radicado).subscribe((response: ReturnModelObtenerSolicitudes) => {
        if (response.result.solicitudSalidaObras.length > 0) {
          this.items = [];
          sessionStorage.clear();
          for (const solicitud of response.result.solicitudSalidaObras) {
            this.items.push(solicitud);
            // this.almacenarDatosStorage(solicitud);
          }
          this.mensajeConsultaSinResultados = false;
          this.establecerAreaNotificaciones('4');
        } else {
          if (response.success === false) {
            this.mensajeConsultaSinResultados = true;
            this.titulo = "Lo sentimos";
            this.currentMessage = 'El servicio no se encuentra disponible en este momento, te recomendamos:\n\n1. Intentarlo más tarde\n2. Recargar la página.';
          }
          else {
            this.mensajeConsultaSinResultados = true;
            this.titulo = "Solicitud no Encontrada";
            this.currentMessage = 'El número de radicación y/o número de documento no fueron encontrados\n en nuestra Base de datos';
          }
        }
      }, error => {
        console.error(error);
      });

    } else {
      this.invalidForm = true;
      return;
    }
  }

  verResultado() {
    this.consultarVerConcepto = !this.consultarVerConcepto;
  }

  limpiarCampos() {
    this.seleccionForm.reset();
    this.invalidForm = false;
    this.verInformacionDescargar = false;
    this.consultarVerConcepto = false;
    this.items = [];
  }

  verEditar(solicitud: Solicitudsalidaobra) {
    this.solicitudProvider.Solicitud = solicitud;
    this.router.navigate(['mincultura/autorizar-exportacion/ingresar-solicitud']);
  }

  verDescargar() {
    this.verInformacionDescargar = !this.verInformacionDescargar;
  }

  ngOnInit() {
    this.visualizacion = false;
    this.seleccionForm = this.formBuilder.group({
      numero_radicado: ['', Validators.required],
      numero_documento: ['', Validators.required]
    });

    this.seleccionSolucionForm = new ConsultarSolicitudForm();
    this.seleccionForm = this.seleccionSolucionForm.getForm();
  }

  obtenerReporte(content, sosId: number) {
    const reporte = {
      SosId: sosId
    } as RequestModelObtenerReporte;
    this.service.obtenerReporte(reporte).subscribe((response: any) => {
      if (response.success === true) {
        const data = response.result.listaReportes[0] as Listareporte;
        this.reporteRespuestaSolicitud = data.reportes[0];
        this.reportesObras = data.reportes.filter(x => x.nombreArchivo.indexOf("Respuesta Obra") !== -1);
        this.modalService.open(content, { size: 'xl', scrollable: true });
      }
    }, error => {
      console.error(error);
    });
  }

  visualizarArchivo(archivo: Reporte) {
    this.visualizacion = true;
    this.archivoVisualizado = archivo;
    const linkSource = 'data:application/pdf;base64,' + archivo.archivo;
    this.src = linkSource;
  }

  descargarArchivo() {
    this.archivoVisualizado;
    const linkSource = 'data:application/pdf;base64,' + this.archivoVisualizado.archivo;
    const downloadLink = document.createElement('a');
    const fileName = this.archivoVisualizado.nombreArchivo;
    downloadLink.href = linkSource;
    downloadLink.download = fileName;
    downloadLink.click();
  }

  closeModal() {
    this.modalService.dismissAll();
  }
}
