import { Component, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

import { AutorizarExportacionUtilService } from '../../services/autorizar-exportacion-util.service';
import { SesionProvider } from '../../../providers/sesion';
import { Sesion } from '../../models/sesion';
import { Solicitudsalidaobra } from '../../models/solicitudsalidaobra';
import { SolicitudProvider } from '../../../providers/solicitud';

@Component({
  selector: 'app-iniciar-sesion',
  templateUrl: './iniciar-sesion.component.html',
  styleUrls: ['./iniciar-sesion.component.scss']
})
export class IniciarSesionComponent implements OnInit {
  formGroupLogin: FormGroup;
  typeButton = 'password';
  invalidForm = false;

  constructor(
    public modalService: NgbModal,
    private formBuilder: FormBuilder,
    public sesionProvider: SesionProvider,
    public service: AutorizarExportacionUtilService,
    private solicitudProvider: SolicitudProvider) {

    this.formGroupLogin = this.formBuilder.group({
      formControlUsuario: ['', Validators.required],
      formControlPassword: ['', Validators.required]
    });

    const sesion = {} as Sesion;
    this.sesionProvider.Sesion = sesion;
  }

  open(content) {
    this.modalService.open(content,
      {
        size: 'lg',
        backdrop: 'static',
        keyboard: false
      });
  }

  cancelar() {
    this.modalService.dismissAll();
  }

  login() {
    if (this.formGroupLogin.value.formControlUsuario === '' || this.formGroupLogin.value.formControlPassword === '') {
      this.invalidForm = true;
      return;
    }

    this.service.IniciarSesion(this.formGroupLogin.value.formControlUsuario,
      this.formGroupLogin.value.formControlPassword).subscribe(dataSesion => {
        if (dataSesion.success === true) {
          this.sesionProvider.Sesion.Token = dataSesion.result.token;
          this.sesionProvider.Sesion.Usuario = this.formGroupLogin.value.formControlUsuario;
          this.sesionProvider.Sesion.Contrasena = this.formGroupLogin.value.formControlPassword;
          this.solicitudProvider.Solicitud = {
            sosId: 0
          } as Solicitudsalidaobra;
          this.modalService.dismissAll();
          this.asignarVariables();
        }
        this.invalidForm = !dataSesion.success;
      });
  }

  asignarVariables() {
    this.invalidForm = false;
    this.service.asignarPaso(1);
    this.service.asignarpasoIngresar(1);
    this.service.asignarLlega(1);
  }

  ngOnInit() { }

  checkShowPassword() {
    this.typeButton = this.typeButton === 'password' ? 'input' : 'password';
  }

}
